* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body, #root, .app-container {
  min-height: 100vh;
  width: 100%;
  overflow-x: hidden;
  background-color: #2E1A47;
  color: #ffffff;
  padding: env(safe-area-inset-top) env(safe-area-inset-right) env(safe-area-inset-bottom) env(safe-area-inset-left);
}

.content-container {
  flex: 1;
  overflow-y: auto;
  padding-bottom: 5rem; /* Динамический отступ для навбара */
}

.main-content {
  padding: 3vw;
}

.header-image {
  background: linear-gradient(135deg, #4B2E83, #F5C563);
  min-height: 15vh;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1rem;
  margin-bottom: 2vh;
  position: relative;
}

.header-title {
  font-size: clamp(1.5rem, 5vw, 2rem);
  font-weight: bold;
  text-align: center;
}

.button-container {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: space-between;
  margin-bottom: 2vh;
}

.sleep-button {
  background: #F5C563;
  color: #2E1A47;
  border: none;
  padding: 0.8rem 1.5rem;
  border-radius: 0.8rem;
  font-size: clamp(0.9rem, 3vw, 1.1rem);
  cursor: pointer;
  flex: 1;
  min-width: 40%;
}

.story-card {
  display: flex;
  align-items: center;
  background: #4B2E83;
  border-radius: 1rem;
  margin-bottom: 1.5vh;
  padding: 1rem;
  cursor: pointer;
}

.story-cover {
  width: clamp(3rem, 12vw, 4rem);
  height: clamp(3rem, 12vw, 4rem);
  border-radius: 0.8rem;
  margin-right: 1rem;
  flex-shrink: 0;
}

.story-info {
  flex: 1;
  overflow: hidden;
}

.story-title {
  font-size: clamp(1rem, 4vw, 1.2rem);
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.story-author {
  font-size: clamp(0.8rem, 3vw, 0.9rem);
  color: #E5989B;
  margin: 0;
}

.play-icon {
  background: none;
  border: none;
  color: #F5C563;
  font-size: clamp(1.2rem, 4vw, 1.5rem);
  cursor: pointer;
}

.pulse {
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.2); }
  100% { transform: scale(1); }
}

.player-overlay {
  position: fixed;
  top: env(safe-area-inset-top);
  left: env(safe-area-inset-left);
  right: env(safe-area-inset-right);
  bottom: env(safe-area-inset-bottom);
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.player-container {
  background: #2E1A47;
  border-radius: 1rem;
  padding: 3vw;
  width: 90%;
  max-width: 90vw;
  text-align: center;
}

.player-cover {
  width: 100%;
  height: clamp(10rem, 40vw, 15rem);
  border-radius: 1rem;
  margin-bottom: 2vh;
}

.player-title {
  font-size: clamp(1.2rem, 4vw, 1.5rem);
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.player-author {
  font-size: clamp(0.9rem, 3vw, 1rem);
  color: #E5989B;
  margin: 1rem 0;
}

.player-controls {
  display: flex;
  justify-content: center;
  gap: clamp(1rem, 4vw, 1.5rem);
  margin: 2vh 0;
}

.player-button {
  background: none;
  border: none;
  cursor: pointer;
}

.play-button {
  background: #F5C563;
  border-radius: 50%;
  width: clamp(3rem, 12vw, 4rem);
  height: clamp(3rem, 12vw, 4rem);
  display: flex;
  justify-content: center;
  align-items: center;
}

.time {
  display: flex;
  justify-content: space-between;
  font-size: clamp(0.8rem, 3vw, 0.9rem);
}

.progress-bar {
  position: relative;
  height: 0.4rem;
  background: #4B2E83;
  border-radius: 0.4rem;
  margin: 1rem 0;
  cursor: pointer;
}

.progress {
  height: 100%;
  background: #F5C563;
  border-radius: 0.4rem;
}

.thumb {
  position: absolute;
  top: -0.4rem;
  width: 1rem;
  height: 1rem;
  background: #F5C563;
  border-radius: 50%;
  transform: translateX(-50%);
}

.action-button {
  background: #F5C563;
  color: #2E1A47;
  border: none;
  padding: 0.8rem 1.5rem;
  border-radius: 0.8rem;
  cursor: pointer;
  font-size: clamp(0.9rem, 3vw, 1rem);
  margin: 1rem;
}

.top-left {
  position: absolute;
  top: 1rem;
  left: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.mini-player {
  position: fixed;
  bottom: calc(5rem + env(safe-area-inset-bottom));
  left: env(safe-area-inset-left);
  right: env(safe-area-inset-right);
  background: #4B2E83;
  display: flex;
  align-items: center;
  padding: 1rem;
  z-index: 1000;
}

.mini-player-cover {
  width: clamp(2.5rem, 10vw, 3rem);
  height: clamp(2.5rem, 10vw, 3rem);
  border-radius: 0.5rem;
  margin-right: 1rem;
  flex-shrink: 0;
}

.mini-player-info {
  flex: 1;
  overflow: hidden;
}

.mini-player-title {
  font-size: clamp(0.8rem, 3vw, 0.9rem);
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.mini-player-author {
  font-size: clamp(0.7rem, 2.5vw, 0.8rem);
  color: #E5989B;
  margin: 0;
}

.mini-player-controls {
  display: flex;
  gap: 0.8rem;
}

.nav-bar {
  position: fixed;
  bottom: env(safe-area-inset-bottom);
  left: env(safe-area-inset-left);
  right: env(safe-area-inset-right);
  background: #4B2E83;
  display: flex;
  justify-content: space-around;
  padding: 0.8rem 0;
  z-index: 1000;
}

.nav-button {
  background: none;
  border: none;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: clamp(0.7rem, 2.5vw, 0.8rem);
  cursor: pointer;
}

.nav-button.active {
  color: #F5C563;
}

.modal-overlay {
  position: fixed;
  top: env(safe-area-inset-top);
  left: env(safe-area-inset-left);
  right: env(safe-area-inset-right);
  bottom: env(safe-area-inset-bottom);
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: #2E1A47;
  border-radius: 1rem;
  padding: 3vw;
  width: 90%;
  max-width: 90vw;
  max-height: 80vh;
  overflow-y: auto;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.modal-title {
  font-size: clamp(1.2rem, 4vw, 1.5rem);
  margin: 0;
}

.modal-close-button {
  background: #F5C563;
  color: #2E1A47;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  cursor: pointer;
  font-size: clamp(0.8rem, 3vw, 0.9rem);
}

.modal-body {
  font-size: clamp(0.8rem, 3vw, 0.9rem);
  line-height: 1.5;
}

.modal-body h3 {
  font-size: clamp(1rem, 3.5vw, 1.2rem);
  margin-top: 1.5rem;
}

.modal-body ul {
  padding-left: 1.5rem;
}